.app {
  max-width: 860px;
  margin: 0px auto;
  text-align: center;
}

.title {
  width: 80%;
  margin-bottom: 30px;
}

button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background: #c23838;
  color: #fff;
}
.cards-grid {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px 20px;
}
