@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Roboto", sans-serif;
  background: #2b2d42;
  color: #fff;
}

body {
  margin: 0;
  font-size: 1.5em;
}

.card {
  position: relative;
}
.card img {
  width: 100%;
  height: 100%;
  display: block;
  border: 2px solid #fff;
  border-radius: 6px;
}
.card .front {
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position: absolute;
}
.flipped .front {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  transition-delay: 0.2s;
}
.card .back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
  cursor: pointer;
}
.flipped .back {
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
  transition-delay: 0s;
}

.app {
  max-width: 860px;
  margin: 0px auto;
  text-align: center;
}

.title {
  width: 80%;
  margin-bottom: 30px;
}

button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background: #c23838;
  color: #fff;
}
.cards-grid {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px 20px;
}

.popup-container {
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  /* display: flex; */
  display: none;
  align-items: center;
  justify-content: center;
}

.popup {
  background: rgba(230, 57, 70, 1);
  border-radius: 20px;
  box-shadow: 0 15px 10px 3px rgba(0, 0, 0, 0.5);
  padding: 20px;
  text-align: center;
  max-width: 300px;
  border: solid black 5px;
}
.popup h1 {
  margin: 10px;
}
.image-container {
  margin-bottom: 20px;
  opacity: 1;
}
.image-container img:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.popup button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
.popup button:hover {
  background: #f1faee;
  color: #1d3557;
}

